*{
  box-sizing: border-box;
  margin: 0;
  padding:0;
}
body{
display: flex;
justify-content: center;
background-color: #23252C;
}
.container{
  background-color: #F5F5F5;
  max-width: 35ch;
  border-radius: 0.5em;
  margin: 5em;
}
main{
  text-align: center;
  color: #2B283A;
}
main>h1{
  margin-top: 0.5em;
}
main >h2{
 color: #F47D27;
 margin:0.7em 0;
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 15px;
}
main>p{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 150%;
  color: #4A4E74;
  margin-bottom: 1.2em;
}
.about,.interests{
  padding: 20px;
  font-size: 10.24px;
  color: #4A4E74;
}
.about>h3,.interests>h3{
font-size: 20px;
margin-bottom: 0.2em;
font-weight: 700;
color: #2B283A;
}
img{
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
}
.links{
display: flex;
gap: 8px;  
justify-content: center;
align-items: center;
}
.email{
  text-decoration: none;
  display: flex;
  align-self: center;
   padding: 5px 20px 5px 20px;
  background: #FFFFFF;
  font-weight: 500;

  border: 1px solid #D1D5DB;
  color: #374151;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px
}
.linkedin{
  text-decoration: none;
  padding: 5px 20px 5px 20px;
  background: #297FE5;
  font-weight: 500;

 color: #fff;
 border: 1px solid #D1D5DB; 
 box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
 border-radius: 6px
}
.linkedin>svg{
  margin-right: 0.5em;
}
.email>svg{
  margin-right: 0.5em;
}
footer{
  padding: 1em;
  background: #D5D4D8;
  border-bottom-left-radius:  0.5em;
  border-bottom-right-radius:  0.5em;
  display: flex;
  gap: 1.5em;
  justify-content: center;

}